* {
  box-sizing: border-box;
}

html {
  --black: #000000;
  --gray: #dddddd;
  --white: #f4ecdd;
}

body {
  background-color: var(--white);
  font-family: arial, sans-serif;
  margin: 0;
}

figure:has(iframe) {
  margin: 0;
  padding: 0;
  width: 90vw;

  iframe {
    height: var(--height--max);
  }
}
